import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '../components/index/Home.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    }
  ]
})



