<template>
    <div class="app-home">
      <!-- 侧滑导航根容器 -->
<div class="mui-off-canvas-wrap mui-draggable">
    <!-- 菜单容器 -->
    <aside class="mui-off-canvas-left">
      <div class="mui-scroll-wrapper">
        <div class="mui-scroll">
          <!-- 菜单具体展示内容 -->
          ...
        </div>
      </div>
    </aside>
    <!-- 主页面容器 -->
    <div class="mui-inner-wrap">
      <!-- 主页面标题 -->
      <header class="mui-bar mui-bar-nav">
        <button class="mui-icon mui-action-menu mui-icon-bars mui-pull-left" style="border:none;background:none" @click="showMenu"></button>
        <!-- <h1 class="mui-title">家居商城</h1> -->
        <input type="text"  placeholder="搜索">
        <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/banners/1.png?sign=81870c79823a5c7731d74b1ae0598911&t=1553429424">
      </header>
      <div class="mui-content mui-scroll-wrapper">
        <div class="mui-scroll">
          <!-- 轮播图 -->
          <mt-swipe>
            <mt-swipe-item v-for="item in list" :key="item.swipe_id">
              <img :src="item.swipe_path" />
            </mt-swipe-item>
          </mt-swipe>
          <!-- 四宫格 -->
        <ul class="mui-table-view mui-grid-view mui-grid-12">
            <li class="mui-table-view-cell mui-media mui-col-xs-3 mui-col-sm-3" v-for="item of icon" >
              <router-link to=""> 
              <img :src="item.squares_path" @touchstart="touchRotate" :data-index='item.squares_id' 
              :style="{'transform':item.squares_id==img_index ? 'rotateY(360deg)':'none'}" 
              >
              <!-- 判断如果当前下标 等于 点击下标 就让对应的图标旋转 -->
                <div class="mui-media-body">{{item.squares_title}}</div>
              </router-link>
            </li>
        </ul>
          <!-- 标题新品推荐 -->
          <div class="menu-title">
              <div>
                <span>新品推荐 </span>
                <span>| NEW PRODUCT</span>
              </div>
              <div>
                <span>更多 ＞</span>
              </div>
          </div>
          <!-- 滑块 -->
          <div class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
            <div class="mui-scroll">
              <a class="mui-control-item mui-active" style="position: relative;">
                <div style="position: absolute;width:45px;height:35px;background: #04cd88;right:0px;top:10px" class="greenBox">
                  <img id="imgStyle" src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/heart.png?sign=2963318f7bb51af7912e15556f5ac447&t=1553513968">
                </div>
                <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/01b1c65c3439c5a80121df90aeb9c2.jpg@1280w_1l_2o_100sh.jpg?sign=236eb6b816e3f6f576006e795e74e3f5&t=1553431435">
                <div>
                  <p>舒适沙发</p>
                  <p>￥5799.00 <span>￥8799.00</span></p>
                </div>
              </a>
              <a class="mui-control-item">
                <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/01b9245c3439c4a80121df905e35e3.jpg@1280w_1l_2o_100sh.jpg?sign=e92777df8485fa1e2c25951c363a2e7f&t=1553431462">
              </a>
              <a class="mui-control-item">
                  <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/010b565c3439c4a8012029ac513898.jpg@1280w_1l_2o_100sh.jpg?sign=cda1b9877d2f17eb1ebdf294c58c50ec&t=1553435032">
              </a>
              <a class="mui-control-item">
                  <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/01269d5c3439c3a80121df909c4464.jpg@1280w_1l_2o_100sh.jpg?sign=9d4a6783a30c823ca2a6b66f726bbe5a&t=1553435052">
              </a>
              <a class="mui-control-item">
                  <img src="https://6d63-mc-dc4b7e-1258736979.tcb.qcloud.la/vue/scroll/017ce95c3439c4a8012029ac82bd44.jpg@1280w_1l_2o_100sh.jpg?sign=5e0c3bc273724d2a71ef4a4e5cd7e8aa&t=1553435061">
              </a>
          </div>
        </div>
        </div>
      </div>  
    </div>
  </div>
        <!-- 顶部导航 -->
        
        
    </div>
</template>

<script>
export default {
    data(){
        return {
            img_index:"",
            list:[],
            icon:[]
        }
    },
    methods: {
        getSwipe(){
            var url = "http://localhost:3001/index/swipe";
            this.axios.get(url).then(result=>{
              this.list = result.data
            })
        },
        getIcon(){
          var url = "http://localhost:3001/index/squares";
          this.axios.get(url).then(result=>{
             //console.log(result.data);
            this.icon = result.data
          })
        },
        showMenu(){
          this.mui('.mui-off-canvas-wrap').offCanvas().toggle();
        },
        touchRotate(e){
          this.img_index = e.target.dataset.index;
          console.log(this.img_index);
        }
    },
    created() {
        this.getSwipe()
        this.getIcon()
    },
}
</script>

<style scoped>
.mui-content{
  background: #fff;
}
.app-home{
  height:1000px;
  width:100%;
  background: #fff;
}
.mui-bar{
  display: flex;
  align-items: center;
  background: #f4f4f4;
  /* border:1px solid red; */
}
.mui-bar .mui-icon{
  font-size:22px;
}
.mui-bar-nav>input{
  height:28px;
  margin:0;
  padding:0;
  width:80%;
  border-radius: 40px;
  display: flex;
  margin-left:18px;
  text-align: center;
}
.mui-bar-nav>img{
  width:30px;
  height:30px;
  margin-left:8px;
}
/*轮播图*/
.mint-swipe-indicator{
  border-radius:0% !important;
}
.app-home .mint-swipe{
  height:200px;
}
.app-home .mint-swipe img{
  width:100%;
  height:200px;
}
.mui-table-view{
  width:96%;
  margin: 0 auto;
}
/*九宫格  图片宽度*/
.app-home .mui-grid-12{
  position: relative;
  top:-13px;
  background:#fff;
  border-radius: 5px;
  box-shadow: 3px 7px 14px -6px;
}
.app-home .mui-grid-12 img{
  width:40px;
  height:40px;	 
  transition: 1s linear;
}
.mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body{
  font-size:12px;
  color:#444;
}
/*九宫格  背景修改为白色*/
.app-home .mui-grid-view.mui-grid-9{
  background-color:#fff; 
}
/*标题栏 新品推荐 */
.menu-title{
  display: flex;
  flex-flow: row nowrap;
  white-space: nowrap;
  justify-content: space-between;
  padding: 5px 15px;
}
.menu-title>div:first-child>span:first-child{
  color:#04cd88;
  font-size:18px;
  font-weight: 900;
}
.menu-title>div:first-child>span:last-child{
  font-size:13px;
  color:#666666;
  margin-left: 6px;
}
.menu-title>div:last-child>span:first-child{
  font-size:14px;
  color:#333;
}
/* 滑动区块 */
.greenBox{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#imgStyle{
  height:20px !important;
  width:20px !important;
  border:1px solid green;
}
.mui-segmented-control.mui-scroll-wrapper{
  height:200px;
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item{
  /* border:1px solid red; */
  border:1px solid red;
  height:200px;
  margin-left:15px;
  padding: 0;
}
.mui-segmented-control .mui-control-item{
  line-height: 12px;
  text-align: left;
  /* border:1px solid #999; */
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item img{
  width:300px;
  height:150px;
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item div{
  width:100%;
  padding:6px;
  /* border:1px solid red; */
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item div p:first-child{
  color:#666;
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item div p:last-child{
  color:#333;
}
.mui-segmented-control.mui-scroll-wrapper .mui-control-item div p:last-child>span{
  color:#999;
  text-decoration: line-through;
}
</style>


